html,
body {
  margin: auto;
	font-size: 12px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #222;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

canvas {
	margin: auto;
	top: 0; bottom: 0;
	left: 0; right: 0;
	position: absolute;
	border: 1px solid #000;
}

button {
	margin-top: 0;
	padding: 10px;
}

input {
	margin-left: 20px;
	padding: 8px;
}

div {
  /* pointer-events: none; */
	user-select: none;
}

.App {
	z-index: 1;
	margin: auto;
	width: 1200px;
	height: 550px;
	top: 0; bottom: 0;
	left: 0; right: 0;
	position: absolute;
	/* pointer-events: none; */
	/* background-color: red; */
}

.no-pointer-events {
	pointer-events: none;
}

.mouse-position-ui {
	color: #fff;
	top: 0;
	right: 0;
	position: absolute;
}

.weapon-mod-data {
  right: 0;
  color: #fff;
  position: absolute;
}

.mouse-state-ui {
	color: #fff;
	top: 14px;
	right: 0;
	position: absolute;
}

.body-data {
	color: #fff;
	padding: 4px;
	display: inline-block;
}

.debug-ui {
	color: #fff;
  pointer-events: none;
}

.total-ammo-count-ui {
	color: #fff;
	font-size: 14px;
	font-weight: bold;
	text-shadow: #000 1px 1px 1px;
	position: absolute;
	text-align: right;
	right: 24px;
	bottom: 92px;
}

.total-credits-ui {
  color: #fff;
	font-size: 13px;
	font-weight: bold;
	text-shadow: #000 2px 2px 2px;
	position: absolute;
	text-align: right;
	right: 20px;
	bottom: 2px;
}

.Toastify__toast--dark {
	font-size: 16px;
	font-weight: bold;
	text-shadow: rgba(0,0,0,0.5) 2px 2px 2px;
}

#ragdolls-count {
	top: 0;
	right: 0;
	padding: 8px;
	position: absolute;
}